import React from 'react'
import './index.less'
import {Radio} from 'antd';
import {Link, Redirect, Route} from "react-router-dom";
import TaskAceptedCell from "./TaskAceptedCell";

export default class TaskAcepted extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        }
    }
    handleChange = (vl) => {
        const v = vl.target.value;
        console.log(vl);
        console.log(v);
        this.setState({
            tpl: v === 'all' ? undefined : v,
        });
    };

    render() {
        return (
            <div className='my-favs'>
                {/*  */}
                <div className='nav-route'>
                    <Link
                        to={`${this.props.match.url}/SortByPost`}
                        className={document.location.hash.includes('SortByPost') ? 'active' : 'unactive'}
                    >
                        
                    </Link>
                    <Link
                        to={`${this.props.match.url}/SortByDone`}
                        className={document.location.hash.includes('SortByDone') ? 'active' : 'unactive'}
                    >
                        
                    </Link>
                </div>
                <div className='tpl-select'>
                    <Radio.Group defaultValue='all' onChange={this.handleChange}>
                        <Radio.Button value='all'></Radio.Button>
                        <Radio.Button value="1"></Radio.Button>
                        <Radio.Button value="2"></Radio.Button>
                        <Radio.Button value="3"></Radio.Button>
                        <Radio.Button value="0"></Radio.Button>
                    </Radio.Group>
                </div>
                {/*  */}
                <div className='routes-container'>
                    <Route exact path={this.props.match.url} render={() => (
                        <Redirect to={`${this.props.match.url}/SortByPost`}/>
                    )}/>
                    <Route
                        exact
                        path={`${this.props.match.url}/SortByPost`}
                        component={() => <TaskAceptedCell tpl={this.state.tpl} sort='post'/>}
                    />
                    <Route
                        exact
                        path={`${this.props.match.url}/SortByDone`}
                        component={() => <TaskAceptedCell tpl={this.state.tpl} sort='done'/>}
                    />
                </div>
            </div>
        )
    }
}
