import React from 'react'
import './index.less'
import {Avatar, Button, Modal, Spin} from 'antd';
import {baseUrl} from "../../../../../config";
import moment from 'moment'
import {UploadWorkDone} from "./UploadWorkDone";
import {Link, Redirect, Route} from "react-router-dom";
import store from './store'
import {observer} from "mobx-react";

@observer
class TaskAceptedCell extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            myFavs: [],
            contentModal: '',
            visibleCommunications: false,
            worksIng: [],
            worksExpired: [],
        };
        // store state
        store.fetchWorks();
        // 
        store.setTPL(this.props.tpl);
        // 
        store.setSort(this.props.sort);
    }
    renderMyWorks = ()=>{
        const ws = store.worksByTpl;
        const cpns = [];
        for(const key in ws){
            const p = ws[key];
            cpns.push(
                <div
                    className=
                        {
                            p.stop_at < moment().unix()
                                ? 'cell-work-index cell-disable'
                                : 'cell-work-index'
                        }
                    id={p.id}>
                    <div className='work-cell-row'>
                        {/**/}
                        <span className='work-name'>{p.work_name}</span>
                        {/**/}
                        <span
                            className='work_money'>:{p.work_money !== 0 ? p.work_money : ''}{p.work_tpl === 2 || p.work_tpl === 3 ? '' : ''}</span>
                    </div>
                    <div className='work-cell-row'>
                        <span> </span>
                        <span>:{p.work_person_number}</span>
                    </div>

                    <div className='work-cell-row'>
                        {/**/}
                        <span className='work_tpl'>{this.type_convert(p.work_tpl)}</span>
                        {p.stop_at ?
                            <span>:{
                                moment.unix(p.stop_at).format("HH:mm") === '23:59' && moment.unix(p.stop_at).format('MMDD') === moment().format("MMDD") ?
                                    ''
                                    : moment.unix(p.stop_at).format("MM-DD HH:mm")
                            }

                                </span>
                            : null}
                    </div>
                    <div className='work-cell-row'>
                        {/**/}
                        <span>

                        <Avatar size={32} shape='square' src={`${baseUrl}/${p.senter_img}`}
                                className='avatar'></Avatar>
                            {/**/}
                            <span className='name'>{p.senter_name}</span>
                        </span>
                        {/**/}
                        {
                            //
                            p.stop_at < moment().unix()
                                ? <Button disabled></Button>
                                : <div>
                                    <Button
                                        className='button-margin'
                                        type='primary'
                                        onClick={this.showCommunications.bind(this, p.id)}></Button>
                                    <Button
                                        className='button-margin'
                                        type='primary'
                                        onClick={this.uploadWork.bind(this, p.id)}></Button>
                                </div>
                        }
                        {/**/}
                        <span className='button-more'>
                            <Link to={`/w/${p.id}`}>
                                <Button
                                ></Button>

                                </Link>
                            </span>
                    </div>
                </div>
            )
        }
        return cpns
    };
    getCommunications = (id) => {
        const ws = store.worksByTpl;
        for (const w in  ws) {
            const p = ws[w];
            // console.log(p);
            if (p.id === id) {
                return (
                    <div>
                        <h2 className='phone'>:{p.communications.phone}</h2>
                        <img src={`${baseUrl}/${p.communications.wx_png}`} alt=''
                             className='avatar'
                             width='100%'
                        />
                        <h5></h5>
                    </div>
                )
            }
        }
    };
    showCommunications = (id) => {
        console.log(id);
        this.setState({
            visibleCommunications: true,
            contentModal: this.getCommunications(id),
        });
    };
    uploadWork = (id) => {
        console.log(id);
        localStorage.id_work = id;
        this.setState({
            visibleCommunications: true,
            contentModal: <UploadWorkDone/>,
        });
    };
    handleOk = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    handleCancel = () => {
        this.setState({
            visibleCommunications: false,
        });
    };
    type_convert = (type) => {
        switch (type) {
            case 1:
                return "";
            case 2:
                return "";
            case 3:
                return "";
            case 0:
                return "";
            default:
                return "";
        }
    };
    render() {
        return (
                <div className='my-works-accepted'>
                    {this.renderMyWorks()}
                    {/**/}
                    <Modal
                        visible={this.state.visibleCommunications}
                        onOk={this.handleOk}
                        onCancel={this.handleCancel}
                        style={{textAlign: 'center'}}
                    >
                        {this.state.contentModal}
                    </Modal>
                </div>
        )

    }
}
export default TaskAceptedCell;